@charset "UTF-8";
#customize-theme-controls .customize-pane-child:is(.accordion-section-content, .customize-pane-child.accordion-sub-container) {
  height: 100%;
}

#customize-theme-controls .accordion-section-title,
#customize-outer-theme-controls .accordion-section-title {
  border-top: 1px solid #ddd;
  margin-top: -1px;
}

#accordion-section-themes + .control-section,
#customize-theme-controls .control-panel-content:not(.control-panel-nav_menus) .control-section:nth-child(2), #customize-theme-controls .control-panel-nav_menus .control-section-nav_menu, #customize-theme-controls .control-section-nav_menu_locations .accordion-section-title {
  border-top: none;
}

div.customize-section-description {
  margin: 0 -12px;
  padding: 12px 15px;
  background-color: #fff;
  border-bottom: 1px solid #ddd;
}

.customize-control {
  position: relative;
  margin-bottom: 15px;
}
.customize-control, .customize-control *, .customize-control *:before, .customize-control *:after {
  box-sizing: border-box;
}
.customize-control .customize-control-notifications-container:empty {
  display: none;
}

.suki-icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  line-height: 1;
  text-align: center;
}
.suki-icon svg {
  display: inline-block;
  width: 100%;
  height: 100%;
  vertical-align: top;
}
.suki-icon svg:not([stroke-width]) {
  stroke-width: 0;
}
.suki-icon svg:not([stroke]) {
  stroke: currentColor;
}
.suki-icon svg:not([fill]) {
  fill: currentColor;
}

.wp-core-ui .button {
  font-style: normal;
}

:is(.components-input-control, .components-select-control) {
  gap: 2px !important;
}
:is(.components-input-control, .components-select-control) > .components-flex-item:empty {
  display: none;
}

.components-select-control .components-select-control__input {
  line-height: normal !important;
}

.components-toggle-control .components-toggle-control__label:empty {
  display: none;
}

.components-color-picker .react-colorful {
  width: 100% !important;
  overflow: visible !important;
}
.components-color-picker .react-colorful + div {
  padding: 0;
}
.components-color-picker .react-colorful + div > * {
  padding: 0;
}
.components-color-picker .react-colorful + div .components-input-control__input {
  height: 30px !important;
  min-height: 30px !important;
}
.components-color-picker .react-colorful__hue,
.components-color-picker .react-colorful__alpha {
  width: 100% !important;
}
.components-color-picker .react-colorful__saturation {
  height: 155px !important;
  margin-bottom: 12px !important;
}

.suki-responsive-tabs {
  padding-block-start: 0;
  padding-inline-end: 0.5em;
  display: flex;
}

.suki-responsive-tabs__button {
  flex: 1;
  display: inline-flex;
  gap: 0.5em;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.suki-responsive-tabs__button .dashicons {
  font-size: 1.2em;
  width: 1em;
  height: 1em;
}

.modal-open #TB_overlay, .modal-open #TB_window {
  z-index: 500001;
}
.modal-open #TB_iframeContent {
  display: block;
}

.wp-full-overlay {
  background-color: revert;
}

.wp-full-overlay-main {
  margin-top: 0 !important;
  box-shadow: 0 0 2rem rgba(0, 0, 0, 0.1);
}

.preview-tablet .wp-full-overlay-main {
  width: 768px;
  height: 1024px;
  margin-left: -384px;
}

.preview-mobile .wp-full-overlay-main {
  width: 414px;
  height: 736px;
  margin-left: -212px;
}

#sub-accordion-section-suki_section_social .customize-control-text .customize-control-description {
  font-size: 14px;
  line-height: 1.75;
  font-weight: 600;
  font-style: normal;
}
#sub-accordion-section-suki_section_social .customize-control-text .customize-control-description .suki-icon {
  font-size: 18px;
  margin-right: 0.5em;
