@media screen and (min-width: 1324px) {
  #customize-control-header_builder_responsive_tabs {
    width: calc(100% + 20px + 20px);
    margin-inline: -20px;
    margin-block-start: -20px;
  }
  #customize-control-header_builder_responsive_tabs .suki-responsive-tabs {
    width: auto;
    padding: 0 20px 0 calc(20px - 0.5em);
  }
  #customize-control-header_builder_responsive_tabs .suki-responsive-tabs__button {
    flex: 0;
  }
}

@media screen and (min-width: 1324px) {
  #customize-control-header_mobile_visibility select {
    width: auto;
    margin-left: 15px;
  }
}

@media screen and (min-width: 1324px) {
  #customize-control-footer_widgets_bar .customize-control-title {
    display: inline-block;
